@import url('https://fonts.googleapis.com/css?family=Source+Sans+Pro:regular,bold,italic&subset=latin,latin-ext');
body {
    background-color: #FFB81C;
    text-align: center;
    font-family: '/Users/raghavgupta/Desktop/UCR/Research/Code/InteractiveJS3/UCR-Research-Grant/Campus_Brand_Toolkit_2020/Fonts/Fira Sans/FiraSans-ExtraLight.ttf';
}

#Lesson {
    text-align: left;
    padding-left: 5em;
    padding-right: 5em;
}

#Quiz {
    text-align: center;
    padding-left: 2em;
}

#material {
    height: 100%;
    width: 50%;
    float: left;
    text-align: center;
}

#Demo {
    height: 40vw;
    width: 45vw;
    float: right;
    padding-top: 2em;
    padding-right: 4em;
}

h1 {
    padding-top: 1em;
    text-align: center;
    color: #003DA5;
    font-size: 40px;
    font-weight: bolder;
}

canvas {
    display: block;
    height: 40vw;
    width: 45vw;
    background-color: #FFB81C;
    float: right;
}


/* NAV */

nav {
    width: 100%;
    float: left;
    margin: 0 0 3em 0;
    padding: 0;
    border-bottom: 1px solid #003DA5;
}


/* TABLE */

#tableheader {
    background-color: black;
    color: white;
    border: white;
    font-style: inherit;
}

#rowFX,
#rowFY {
    background-color: #003DA5;
    color: white;
}

table {
    border-collapse: collapse;
}

table,
th,
td {
    border: 1px solid black;
    padding: 5px;
    height: 10px;
    table-layout: auto;
}

input {
    text-align: center;
}

.center {
    margin-left: auto;
    margin-right: auto;
}

tr:hover {
    background-color: #f5f5f5;
}

th {
    height: 10px;
    width: 200px;
}